	module.exports = {"data":{"markdownRemark":{"html":"<p>So things are going quite well with my Arch + i3 build! I was able to get sound working without much difficulty\nafter simply installing my drivers, tweaking <a href=\"http://www.alsa-project.org/main/index.php/Main_Page\">ALSA</a> and\nthen installing <a href=\"http://www.freedesktop.org/wiki/Software/PulseAudio/\">PulseAudio</a>. The only tweaks I had to really\ndo were as follows:</p>\n<ul>\n<li>The wrong device was being detected as the default sound device, so I had to tweak, if I recall, the\n<code>\\etc\\modprob.d\\alsa-base.conf</code> settings file to manually set the correct default.</li>\n<li>My laptop's sound buttons did not adjust the volume at first, so I had to use <code>xev</code> to identify which key values\nwere mapped to my keyboard's sound buttons.</li>\n</ul>\n<p>After I did those things, my sound worked great! The only remaining initial item I needed to do to call my \"base\"\ninstallation complete was to customize my UI. So, I downloaded the <a href=\"http://feh.finalrewind.org/\">feh</a> image\nviewer, which goes well with i3, and then used it to take screenshots and set my background. I downloaded some\nfonts (<a href=\"https://mozilla.github.io/Fira/\">Fira</a> and <a href=\"http://www.linuxlibertine.org/\">Linux Libertine/Biolinum</a>, in\nparticular), grabbed a plugin for Firefox, and started tweaking some settings. I also grabbed the\n<a href=\"http://docs.xfce.org/apps/terminal/advanced\">Xfce4 Terminal</a>.</p>\n<p>After tweaking a bunch of settings, here is a screenshot I took on Friday:</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/arch_8_15-c8795f75a29668239aab84295aa652d3-3f5a1.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 28.125%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"arch picture\"\n        title=\"\"\n        src=\"/static/arch_8_15-c8795f75a29668239aab84295aa652d3-f8fb9.jpg\"\n        srcset=\"/static/arch_8_15-c8795f75a29668239aab84295aa652d3-e8976.jpg 148w,\n/static/arch_8_15-c8795f75a29668239aab84295aa652d3-63df2.jpg 295w,\n/static/arch_8_15-c8795f75a29668239aab84295aa652d3-f8fb9.jpg 590w,\n/static/arch_8_15-c8795f75a29668239aab84295aa652d3-85e3d.jpg 885w,\n/static/arch_8_15-c8795f75a29668239aab84295aa652d3-d1924.jpg 1180w,\n/static/arch_8_15-c8795f75a29668239aab84295aa652d3-9452e.jpg 1770w,\n/static/arch_8_15-c8795f75a29668239aab84295aa652d3-3f5a1.jpg 2048w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>I've since removed the scrollbars from the terminal windows and adjusted my DPI settings to more accurately fit my\ndisplays. I have to say, I'm really enjoying it so far! Everything looks great, and it seems like a very fitting\nenvironment to develop in. I'm sure I'll have many more additional tweaks, but I'm happy with the results thus far.\nMy next quest is to publish some <a href=\"https://dotfiles.github.io/\">dotfiles</a> on my GitHub account and start tweaking\nEmacs to fit my development paradigms. </p>\n<p>Until next time!</p>","timeToRead":2,"frontmatter":{"date":"August 17, 2015","title":"An Experiment with Arch and i3 Tiling Window Manager - Part 3","author":{"id":"Bedford West"}}}},"pathContext":{"slug":"/blog/arch-pt3"}}

